import * as React from "react"
import { Link } from "gatsby"
import { FileText, Table, Calendar, Image, FileCheck, BookOpen } from "lucide-react"

const formatIcons = {
    "PDF": FileText,
    "Spreadsheet": Table,
    "Google Form": FileCheck,
    "Calendar": Calendar,
    "Canva Template": Image,
    "Printable Signage": FileText,
    "Printable Card": FileText,
    "Door Sign": FileText,
    "Interactive PDF": BookOpen,
    "Strategic Whitepaper": BookOpen,
    "Legal Template": FileText,
    "Crisis Management Guide": BookOpen,
}

const formatColors = {
    "PDF": "var(--color-editorial-accent)",
    "Spreadsheet": "#10b981",
    "Google Form": "#8b5cf6",
    "Calendar": "#f59e0b",
    "Canva Template": "#ec4899",
    "Printable Signage": "#06b6d4",
    "Printable Card": "#06b6d4",
    "Door Sign": "#06b6d4",
    "Interactive PDF": "#3b82f6",
    "Strategic Whitepaper": "#6366f1",
    "Legal Template": "#78716c",
    "Crisis Management Guide": "#ef4444",
}

const ResourceCard = ({ resource, categorySlug }) => {
    const IconComponent = formatIcons[resource.format] || FileText
    const iconColor = formatColors[resource.format] || "var(--color-editorial-accent)"

    return (
        <Link 
            to={`/resources/${categorySlug}/${resource.slug}/`} 
            className="no-underline"
        >
            <div className="border border-[var(--color-editorial-border)] rounded-lg p-6 hover:border-[var(--color-editorial-accent)] hover:shadow-lg transition-all duration-300 bg-white relative overflow-hidden group">
                {/* Status Badge */}
                <div className="absolute top-4 right-4">
                    <span 
                        className="px-3 py-1 rounded-full text-white"
                        style={{ 
                            fontSize: "var(--text-xs)", 
                            backgroundColor: iconColor,
                            fontWeight: 600 
                        }}
                    >
                        {resource.format}
                    </span>
                </div>

                {/* Icon */}
                <div 
                    className="mb-4 text-4xl"
                    style={{ lineHeight: 1 }}
                >
                    <IconComponent 
                        size={36}
                        style={{ color: "var(--color-editorial-black)" }}
                    />
                </div>

                {/* Title */}
                <h3 
                    className="mb-3 text-[var(--color-editorial-black)] group-hover:text-[var(--color-editorial-accent)] transition-colors"
                    style={{ fontSize: "1.25rem", fontWeight: 600, lineHeight: 1.3 }}
                >
                    {resource.title}
                </h3>

                {/* Description */}
                <p 
                    className="text-[var(--color-editorial-grey)]"
                    style={{ fontSize: "var(--text-sm)", lineHeight: 1.6 }}
                >
                    {resource.description}
                </p>

                {/* Hover Effect Gradient */}
                <div className="absolute bottom-0 left-0 right-0 h-1 bg-gradient-to-r from-[var(--color-editorial-accent)] to-[var(--color-editorial-black)] transform scale-x-0 group-hover:scale-x-100 transition-transform duration-300"></div>
            </div>
        </Link>
    )
}

export default ResourceCard
